<template>
  <el-dialog
    class="dialog-d radius-20 dia-new-user-activity"
    modal-class="dialog-m-center anuc-out-no"
    :modelValue="isActivity"
    :destroy-on-close="false"
    :show-close="false"
    append-to-body
    @close="onClose"
  >
    <template #default>
      <div v-loading="loading" class="w-100b h-100b radius-20">
        <img :src="popUpUrl" class="w-100b h-100b radius-20" @click="onGet">
        <img src="@/image/icon_close.webp" class="anuc-close" @click="isActivity=false">
      </div>
    </template>

  </el-dialog>
</template>
<script setup>
const userStore = useUserStore()
let loading = $ref(false)

const props = defineProps({

})

let isActivity = $ref(false)
let popUpUrl = $ref('https://biftest.s3.ap-southeast-1.amazonaws.com/bfprod/14f485b30443ebd960f1d31bdd4d264c.PNG')

const onClose = () => {}

const statusOptions = $computed(() => {
  return {
    0: $t('activity.rewardSuccess'),
    1: $t('activity.rewardLate'),
    2: $t('activity.rewardAlready'),
    3: $t('activity.rewardEnd'),
  }
})

const checkPop = async () => {
  const data = await activityApi.popUpOrNot({})
  if (localStorage.popUpId === data.popUpId + '_' + userStore?.info?.userId) return // 已经弹窗过的不再弹窗
  isActivity = data.show
  popUpUrl = data.popUpUrl
  localStorage.popUpId = data.popUpId + '_' + userStore?.info?.userId
}

const onGet = async () => {
  loading = true
  try {
    const res = await activityApi.newRegisterActivitySubmit({})
    ElMessage.success(statusOptions[res.data.status])

    isActivity = false
  } catch (error) {

  } finally {
    loading = false
  }
}

onBeforeMount(() => {
  checkPop()
})

</script>

<style lang="scss">
.dia-new-user-activity{
  width: 550px;
  height: 660px;
  border-radius: 20px;
  .el-loading-mask{
    border-radius: 20px;
  }
}
.anuc-out-no{
  .el-dialog__header{
    padding:0px !important;
  }
  .el-dialog__body{
    padding:0px !important;
    height:100%;
    width:100%;
  }
}
.anuc-close{
  width: 56px;
  height: 56px;
  position: relative;
  top: 100px;
}
</style>
